import React from "react";
import { makeStyles } from "@material-ui/styles";
import { Button, Typography } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    // height: "100vh",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
    padding: "20px",
    boxSizing: "border-box",
    margin: "auto",
  },

  pr: {
    fontSize: "25px",
    color: "gray",
  },

  title: {
    width: "100%",
  },
}));

const About = () => {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Typography className={classes.title} variant="h3">
        ACERCA DE MÍ..
      </Typography>
      <Typography style={{ width: "100%", marginTop: "10px" }} variant="h4">
        Hola! Soy Felix
      </Typography>
      <p className={classes.pr}>
        Soy un desarrollador autodidacta Argentino de 25 años de edad, me
        apasiona la programación y la forma en la que esta me permite construir
        de todo.
      </p>
      <p className={classes.pr}>
        Me caracterizo por ser una persona curiosa que le gusta aprender cosas
        nuevas siempre, lo que me ayuda a adaptarme a nuevas tecnologías todo el
        tiempo, cosa muy necesaria en esta industria. También soy amante de los
        desafios y considero que las tareas desafiantes son la mejor la mejor
        forma de aprender y desarrollarse profesionalmente en este sector.
      </p>

      <Button
        component="a"
        href="https://drive.google.com/file/d/19O5ElJmQIE-5gwRJIenEhXl6_Y1tuQZ1/view?usp=sharing"
        target="_blank"
        size="large"
        style={{ marginRight: "auto" }}
        variant="contained"
      >
        Descargar cv
      </Button>
    </div>
  );
};

export default About;
