import React from "react";
import { makeStyles } from "@material-ui/styles";
import CustomTabs from "../components/organisms/Tabs";
import TabsDrawer from "../components/organisms/TabsDrawer";
import { Theme, useMediaQuery } from "@material-ui/core";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";

// Sections
import About from "./sections/About";
import Contact from "./sections/Contact";
// import Education from "./sections/Education";
import Projects from "./sections/Projects";
import Skills from "./sections/Skills";

const useStyles: any = makeStyles((theme: Theme) => ({
  root: {
    height: "100%",
    width: "100%",
    display: "flex",
    flexDirection: "row",
  },

  contentDiv: {
    width: "100%",
    height: "100%",
    display: "flex",
    flexDirection: "column",
    overflow: "auto",
    backgroundSize: "cover",
    // boxSizing: "border-box",
    margin: 0,
  },

  text: {
    color: "white",
  },
}));

const Home = () => {
  const classes = useStyles();
  const minScreen = useMediaQuery("(min-width:780px)");

  return (
    <div className={classes.root}>
      <Router basename={process.env.PUBLIC_URL}>
        {minScreen && <CustomTabs />}
        {!minScreen && <TabsDrawer />}
        <div id="content-div" className={classes.contentDiv}>
          <Switch>
            <Route exact path="/about" component={About} />

            <Route exact path="/projects" component={Projects} />

            <Route exact path="/skills" component={Skills} />

            {/* <Route exact path="/education" component={Education} /> */}

            <Route exact path="/contact" component={Contact} />
            <Redirect from="*" to="/about" />
          </Switch>
        </div>
      </Router>
    </div>
  );
};

export default Home;
