import { makeStyles } from "@material-ui/styles";
import { Theme } from "@material-ui/core";
import * as React from "react";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Box from "@mui/material/Box";
import { Avatar, Typography } from "@mui/material";
import { useHistory, useLocation } from "react-router-dom";

const useStyles: any = makeStyles((theme: Theme) => ({
  root: {
    height: "100%",
    width: "250px",
    borderRight: "1px solid #474B4F",
  },

  label: {
    fontFamily: "Ubuntu",
  },

  imgDiv: {
    height: "170px",
    width: "100%",
    // border: "1px solid white",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
  },
}));

const links = ["/about", "/projects", "/skills", "/contact"];

function CustomTabs() {
  const [value, setValue] = React.useState(0);
  const classes = useStyles();
  const history = useHistory();
  const location = useLocation();

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
    history.push(links[newValue]);
  };

  React.useEffect(() => {
    const path = location.pathname;

    setValue(links.indexOf(path));
  }, [location]);

  return (
    <Box sx={{ maxWidth: 480 }} className={classes.root}>
      <div className={classes.imgDiv}>
        <Avatar
          src={
            "https://scontent.fsfn4-1.fna.fbcdn.net/v/t1.6435-9/175553549_802665877334062_477437237280601277_n.jpg?_nc_cat=100&ccb=1-5&_nc_sid=09cbfe&_nc_eui2=AeHrbmeFIYUd-LMjg6tnk39dJTL3FdeM-nQlMvcV14z6dImEIZoQnekNZzqIYIpqGPtlBxPYXtqTjEyaJJ6uLeRZ&_nc_ohc=JiBUJJ0R2NUAX-kK0iU&_nc_ht=scontent.fsfn4-1.fna&oh=3fb9f0ad073663ed36f44edf9ec3325e&oe=61802B49"
          }
          style={{ width: 56, height: 56 }}
        />

        <Typography variant="subtitle1">Felix Horacio Schugurensky </Typography>
        <Typography variant="subtitle2">Fullstack Developer </Typography>
      </div>
      <Tabs
        value={value}
        orientation="vertical"
        onChange={handleChange}
        variant="scrollable"
        scrollButtons={false}
        aria-label="scrollable prevent tabs example"
      >
        <Tab label={<span className={classes.label}>Sobre mi...</span>} />
        <Tab label={<span className={classes.label}>Proyectos</span>} />
        <Tab label={<span className={classes.label}>Conocimientos</span>} />
        {/* <Tab label={<span className={classes.label}>Educación</span>} /> */}
        <Tab label={<span className={classes.label}>Contacto</span>} />
      </Tabs>
    </Box>
  );
}

export default CustomTabs;
