import React from "react";
import { makeStyles } from "@material-ui/styles";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import SkillStepper from "../molecules/SkillStepper";

const frontEndSkills = [
  {
    label: "HTML5",
    description:
      "Lenguaje de marcado usado en para crear la estructura de una pagina web",
  },
  {
    label: "CSS3",
    description:
      "(Hojas de estilo en cascada) Es un lenguaje de estilos para embellecer las páginas",
  },
  {
    label: "Javascript",
    description:
      "Lenguaje de programación utilizado tanto del lado del cliente como del servidor",
  },
  {
    label: "Boostrap, MaterialUI, TailwindsCSS",
    description:
      "Son frameworks de estilos que nos facilitan la creación de páginas web, ya sea, mediante, classes de CSS pre estilizadas o componentes de react ya creados",
  },

  {
    label: "Typescript",
    description: "Superset de javascript que le agrega a este tipado estático",
  },
  {
    label: "React.js",
    description:
      "Framework de Javascript para la creación de interfaces de usuario interactivas, que facilita tanto al programador a la hora de desarrollar como al usuario brindando mejor experiencia.",
  },
];

const backEndSkills = [
  {
    label: "Node.js",
    description:
      "Node.js permite usar Javascript en un entorno fuera del navegador",
  },

  {
    label: "Express.js",
    description:
      "Express.js es un framework usado en Node que facilita la creación y configuración de servidores web",
  },

  {
    label: "Mongoose.js",
    description:
      "Es un ORM que nos permite hacer consultas en la base de datos MongoDB",
  },

  {
    label: "Sequelize.js",
    description:
      "ORM que nos permite hacer consultas a varios tipos de bases de datos SQL",
  },
];

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    // height: "100%",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    boxSizing: "border-box",
    margin: "auto",
  },

  paper: {
    width: "90%",
    height: "auto",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
    "&&": {
      boxSizing: "border-box",
      background: "transparent",
      backdropFilter: "blur(10px)",
    },
    padding: "20px",
    margin: "20px",
  },

  gridContainer: {
    width: "100%",
    height: "100%",
    boxSizing: "border-box",
  },
}));

const Skills = () => {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <Paper variant="outlined" className={classes.paper}>
        <Typography variant="h3">Conocimientos</Typography>

        <Grid container className={classes.gridContainer} spacing={2}>
          <Grid item lg={6} md={6} sm={12} style={{ marginTop: "20px" }}>
            <Typography variant="body1">Front-End</Typography>
            <SkillStepper steps={frontEndSkills} />
          </Grid>

          <Grid item lg={6} md={6} sm={12} style={{ marginTop: "20px" }}>
            <Typography variant="body1">Back-End</Typography>
            <SkillStepper steps={backEndSkills} />
          </Grid>
        </Grid>
      </Paper>
    </div>
  );
};

export default Skills;
