import * as React from "react";
import Box from "@mui/material/Box";
import Drawer from "@mui/material/Drawer";
import { makeStyles } from "@material-ui/styles";
import {
  Avatar,
  IconButton,
  List,
  ListItem,
  ListItemButton,
  Typography,
} from "@material-ui/core";
import MenuOpenIcon from "@mui/icons-material/MenuOpen";
import { useHistory } from "react-router-dom";

type Anchor = "top" | "left" | "bottom" | "right";

const listOptions = [
  {
    label: "Acerca de...",
    route: "/about",
  },

  {
    label: "Proyectos",
    route: "/projects",
  },

  {
    label: "Conocimientos",
    route: "/skills",
  },

  {
    label: "Contacto",
    route: "/contact",
  },
];

const useStyles = makeStyles((theme) => ({
  button: {},
  imgDiv: {
    height: "170px",
    width: "100%",
    // border: "1px solid white",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
  },
}));

export default function TemporaryDrawer() {
  const classes = useStyles();
  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const history = useHistory();

  const toggleDrawer = (anchor: Anchor, open: boolean) => (
    event: React.KeyboardEvent | React.MouseEvent
  ) => {
    if (
      event.type === "keydown" &&
      ((event as React.KeyboardEvent).key === "Tab" ||
        (event as React.KeyboardEvent).key === "Shift")
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const handleClick = (event: any, route: any) => {
    history.push(route);
  };

  const list = (anchor: Anchor) => (
    <Box
      sx={{ width: anchor === "top" || anchor === "bottom" ? "auto" : 250 }}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <div className={classes.imgDiv}>
        <Avatar
          src={
            "https://scontent.fsfn4-1.fna.fbcdn.net/v/t1.6435-9/175553549_802665877334062_477437237280601277_n.jpg?_nc_cat=100&ccb=1-5&_nc_sid=09cbfe&_nc_eui2=AeHrbmeFIYUd-LMjg6tnk39dJTL3FdeM-nQlMvcV14z6dImEIZoQnekNZzqIYIpqGPtlBxPYXtqTjEyaJJ6uLeRZ&_nc_ohc=JiBUJJ0R2NUAX-kK0iU&_nc_ht=scontent.fsfn4-1.fna&oh=3fb9f0ad073663ed36f44edf9ec3325e&oe=61802B49"
          }
          style={{ width: 56, height: 56 }}
        />

        <Typography variant="subtitle1">Felix Horacio Schugurensky </Typography>
        <Typography variant="subtitle2">Fullstack Developer </Typography>
      </div>
      <nav aria-label="secondary mailbox folders">
        <List dense={false}>
          {listOptions.map((option) => (
            <ListItem disablePadding>
              <ListItemButton
                onClick={(event) => handleClick(event, option.route)}
              >
                <Typography variant="h6">{option.label}</Typography>
              </ListItemButton>
            </ListItem>
          ))}
        </List>
      </nav>
    </Box>
  );

  return (
    <React.Fragment key={"anchor"}>
      <IconButton
        size="large"
        className={classes.button}
        style={{ position: "absolute", zIndex: 1000 }}
        onClick={toggleDrawer("left", true)}
      >
        <MenuOpenIcon />
      </IconButton>
      <Drawer
        anchor={"left"}
        open={state["left"]}
        onClose={toggleDrawer("left", false)}
      >
        {list("left")}
      </Drawer>
    </React.Fragment>
  );
}
