import React from "react";
import { makeStyles } from "@material-ui/styles";
import { Grid, Paper, Typography } from "@material-ui/core";
import ProjectCard from "../molecules/ProjectCard";
import calculatorImg from "../assets/Calculator.png";
import celularImg from "../assets/Celular-interface.png";
import superHeroImg from "../assets/Superhero-app.png";

const projectData = [
  {
    image: superHeroImg,
    title: "App de superheroes",
    description:
      "Aplicación para formar un equipo mediante una api de superheroes",
    codeLink: "https://github.com/FelixSchugu/Challenge-alkemy-react",
    liveLink: "https://felixschugu.github.io/superhero-api",
  },

  {
    image: celularImg,
    title: "Interfaces para Servidor de rol",
    description:
      "Creación de interfaces de usuario para servidor de rol en el juego GTA V",
    codeLink: null,
    liveLink:
      "https://www.youtube.com/watch?v=v7ZNt7e0Aas&t=53s&ab_channel=MartinVon",
  },

  {
    image: calculatorImg,
    title: "Calculadora",
    description:
      "Calculadora con capacidad para realizar operaciones combinadas",
    liveLink: "https://codepen.io/FelixSch/pen/KKNExNK",
    codeLink: null,
  },
];

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    height: "",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    margin: "auto",
  },

  paper: {
    width: "85%",
    // height: "80%",
    "&&": {
      backdropFilter: "blur(10px)",
      background: "transparent",
    },
    boxSizing: "border-box",
    display: "flex",
    flexDirection: "column",
    padding: "20px",
    margin: "20px",
  },

  gridContainer: {
    width: "100%",
    height: "100%",
    "&&": {
      marginTop: "20px",
    },
  },

  gridElement: {},
}));

const Projects = () => {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Paper classes={{ root: classes.paper }} variant="outlined">
        <Typography style={{ margin: "auto" }} variant="h3">
          Mis proyectos
        </Typography>
        <Grid className={classes.gridContainer} container spacing={4}>
          {projectData.map((project) => (
            <Grid
              item
              lg={4}
              md={6} 
              sm={6}
              xs={12}
              className={classes.gridElement}
            >
              <ProjectCard
                title={project.title}
                imgSrc={project.image}
                description={project.description}
                codeLink={project.codeLink}
                liveLink={project.liveLink}
              />
            </Grid>
          ))}
        </Grid>
      </Paper>
    </div>
  );
};

export default Projects;
